footer {
	margin-top: auto;
	border-top: 2px solid black;


	& div {
		background-color: #F2E0C9;
		padding: 20px;
		text-align: center;
	}

	& a {
		font-size: 16px;
		font-weight: 600;
		color: #00005c;
		transition: all 0.3s ease 0s;
	}

	& a:hover {
		background-color: #707bf98C;
		box-shadow: 0px 0px 12px 8px #7882fca1;
		border-radius: 10px;
	}
}